.container {
  width: 90%;
  max-width: 1500px;
  height: 100vh;
}

.center-one {
  margin: 0px auto;
}

.container-50 {
  width: 50%;
}

header {
  padding: 3rem 0em;
}

footer {
  padding: 2em 1em;
}

img {
  width: 100%;
  opacity: 0.4;
  transition: opacity 0.3s ease-in;
  border-radius: 0.4em;
}

img:hover {
  opacity: 1;
}
.img-container {
  width: 60%;
}

.h-100 {
  height: 100%;
}

/*
  logo styles
*/

.logo-component {
  width: 25em;
  margin-bottom: 1em;
  text-align: center;
}

.logo-component > hr {
  margin-bottom: 1em;
  color: rgba(255, 255, 255, 0.788);
}

.logo-component .logo-sup {
  font-family: "Meie Script", Arial, Helvetica, sans-serif;
  font-size: 3em;
}

.logo-component .logo-inf {
  font-size: 1.5em;
  letter-spacing: 0.7em;
}

/*
  button styles
*/

.btn {
  display: inline-block;
  padding: 0.7em 1.3em;
  background: #1a1818;
  text-decoration: none;
  border: none;
  color: #fff;
  margin: 0.5em;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

.btn:hover {
  box-shadow: 0px 0px 0.7em rgb(255, 255, 255, 0.4);
}

.btn-rounded {
  border-radius: 1.2em;
}

.btn-rounded-sm {
  padding: 0.7em 1em;
  border-radius: 0.8em;
}

/*
  social
*/
.instagram {
  color: #cc4747;
}

.facebook {
  color: #228db8;
}
/*
  flexbox
*/

.d-flex {
  display: flex;
}

.align-items-center {
  align-items: center;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-space-around {
  justify-content: space-around;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-column {
  flex-direction: column;
}

.title {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 700;
  letter-spacing: 0.3em;
  font-size: 2.5rem;
}

.text {
  font-size: 1.2em;
  line-height: 1.7em;
}

.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 0.5em;
}

.mt-1 {
  margin-top: 2em;
}

.card {
  width: 16em;
  overflow: hidden;
  margin: 0.6em;
}

@media screen and (max-width: 950px) {
  .container-50 {
    width: 100%;
    padding: 1em 0em;
  }
}

@media screen and (max-width: 734px) {
  .header {
    justify-content: center;
  }
}

@media screen and (max-width: 610px) {
  .img-container {
    width: 90%;
  }
}

@media screen and (max-width: 373px) {
  .logo-component .logo-sup {
    font-size: 2.5em;
  }

  .img-container {
    width: 100%;
  }

  .logo-component .logo-inf {
    font-size: 1.2em;
  }
}
